<template>
  <view class="comment-list">
    <view
      v-for="(comment, index) in comments"
      :key="index"
      class="comment-item">
      <view class="comment-author">{{ comment.author }}</view>
      <view class="comment-content">{{ comment.content }}</view>
      <view class="comment-replies">
        <view
          v-for="(reply, replyIndex) in comment.replies"
          :key="replyIndex"
          class="reply-item">
          <view class="reply-author">{{ reply.author }}</view>
          <view class="reply-content">{{ reply.content }}</view>
        </view>
      </view>
      <view class="reply-input">
        <input
          v-model="newReply"
          placeholder="回复..." />
        <button @click="addReply(index)">回复</button>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  props: {
    comments: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      newReply: '',
    };
  },
  methods: {
    addReply(commentIndex) {
      if (this.newReply.trim()) {
        this.comments[commentIndex].replies.push({
          author: '当前用户', // Replace with actual user data
          content: this.newReply,
        });
        this.newReply = '';
      }
    },
  },
};
</script>

<style scoped>
.comment-list {
  padding: 10px;
}
.comment-item {
  margin-bottom: 15px;
}
.comment-author {
  font-weight: bold;
}
.comment-content {
  margin: 5px 0;
}
.comment-replies {
  margin-left: 20px;
}
.reply-item {
  margin-bottom: 5px;
}
.reply-input {
  display: flex;
  align-items: center;
}
.reply-input input {
  flex: 1;
  margin-right: 5px;
}
</style>
